// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin  mkdfStandardPaginationStyle($list_type: null) {
    @include mkdfRelativeHolderLayout(top);
    margin: 0;
    clear: both;

    ul {
        @include mkdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        .mkdf-pl-pagination-next,
        .mkdf-pl-pagination-previous{
            &:hover{
                background-color: transparent;
                a{
                    color: $first-main-color;
                }
            }
        }

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 5px;
            border-radius: 2px;

            &.mkdf-pag-next,
            &.mkdf-pag-last,
            &.mkdf-pag-prev,
            &.mkdf-pag-first{
                &:hover{
                    background-color: transparent;
                }
                a {
                    padding: 0;
                    line-height: 2em;
                    &:hover {
                        background-color: transparent;
                        color: $first-main-color;
                    }
                }
            }

            &.mkdf-pag-last,
            &.mkdf-pag-first{
                span{
                    margin-top: 1px;
                }
            }

            a,span {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                @include mkdfH6();
                font-size: 16px;
                font-weight: 500;
                line-height: 48px;
                padding: 2px 15px 0 16px;
            }

            span.current{
                color: #fff;
                background-color: $first-main-color;
                border-radius: $default-border-radius;
            }

            &.mkdf-pag-active,
            &:hover{

                background-color: $first-main-color;
                
                a {
                    color: #fff;
                    transition: color .1s ease-in-out;
                }
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next,
            &.mkdf-pag-first,
            &.mkdf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 24px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.mkdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                    line-height: 48px;
                    padding: 2px 14px 0 17px;

                    &:hover{
                        color: #fff;
                    }
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;
                        padding: 10px 14px 0 17px;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 24px;
                            line-height: 26px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 16px;
    line-height: 2em;
    letter-spacing: 0.02em;
    font-weight: 400;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out, box-shadow .5s cubic-bezier(0.49, 0.79, 0.17, 0.94);
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 5px 30px;

    } @else if ($size == 'medium') {
        padding: 7px 38px;

    } @else if ($size == 'large') {
        padding: 13px 66px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
    border-radius: 2px;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    box-shadow: $first-hover-box-shadow;
    border-radius: 2px;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfFirstMainButton($important: null){
    background-color: $first-main-color $important;
}

@mixin mkdfFirstMainButtonHover($important: null){
    background-color: $first-main-color $important;
    box-shadow: $hover-box-shadow $important;
}

@mixin mkdfSecondMainButton($important: null){
    background-color: $second-main-color $important;
}

@mixin mkdfSecondMainButtonHover($important: null){
    background-color: $second-main-color $important;
    box-shadow: $first-hover-box-shadow;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

