// heading mixins - start

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();
    margin: 25px 0;
    font-family: $default-text-font;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include mkdfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfBlockquote{
    @include mkdfRelativeHolderLayout();
    margin: 10px 0;
    padding: 57px 40px 60px 40px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    quotes: none;
    box-sizing: border-box;
    border: 1px solid $default-border-color;
    border-bottom: 4px solid $first-main-color;

    @include phone-landscape{
        padding: 57px 57px 60px 20px;
    }

    > * {
        @include mkdfH4();
        margin: 0 20% 0 0;
        text-align: left;

        @include phone-landscape{
            margin: 0 37px 0 0;
        }
    }

    p{
        strong{
            @include mkdfH4();
            color: $default-heading-color;
        }
    }

    &:before{
        content: '';
    }
    &:after {
        content: url(../img/quote.svg);
        position: absolute;
        right: 42px;
        top: 60px;
        width: 70px;

        @include phone-landscape{
            right: 20px;
            top: 60px;
            width: 60px;
        }
    }
    cite {
        font-style: normal;
        font-weight: 400;
        margin-top:40px;
        float: left;
    }
}

