@mixin mkdfHeadingStyle($with-heading: null, $with-color: true) {
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}

@mixin mkdfH1() {
    font-size: 80px;
    line-height: 1em;
    letter-spacing: -0.01em;
    font-weight: 800;
}

@mixin mkdfH2() {
    font-size: 60px;
    line-height: 0.96em;
    letter-spacing: -0.02em;
    font-weight: 600;
}

@mixin mkdfH3() {
    font-size: 40px;
    line-height: 0.75em;
    letter-spacing: 0;
    font-weight: 600;
}

@mixin mkdfH4() {
    font-size: 26px;
    line-height: 1.15em;
    font-weight: 600;
}

@mixin mkdfH5() {
    font-size: 20px;
    line-height: 1.5em;
    font-weight: 600;
}

@mixin mkdfH6() {
    font-size: 14px;
    line-height: 2em;
    letter-spacing: 0.1em;
    font-weight: 500;
}

@mixin mkdfH7() {
    font-size: 12px;
    line-height: 2.33em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-weight: 500;
    color: #565969;
}

@mixin titleb(){
